import React, { useEffect, useState } from 'react';
import { useStaticQuery, graphql } from 'gatsby';
import { Helmet } from 'react-helmet';
import loadable from '@loadable/component';
import Box from '@mui/material/Box';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import '../index.css';

const SectionMenu = loadable(() => import('../components/sections/menu'));
const SectionMainvisual = loadable(() =>
  import('../components/sections/mainvisual')
);
const SectionAbout = loadable(() => import('../components/sections/about'));
const SectionAccess = loadable(() => import('../components/sections/access'));
const SectionContact = loadable(() => import('../components/sections/contact'));

const theme = createTheme({
  palette: {
    primary: {
      main: '#b28223'
    }
  },
  typography: {
    fontSize: 12,
    fontFamily: ['Noto Serif JP', 'YuMincho', 'serif'].join(','),
    h4: {
      fontSize: 32,
      letterSpacing: 1.5
    },
    h5: {
      fontSize: 28
    }
  }
});

const IndexPage = () => {
  const {
    site: {
      siteMetadata: { siteUrl, title, description, tel }
    }
  } = useStaticQuery(graphql`
    query {
      site {
        siteMetadata {
          siteUrl
          title
          description
          tel
        }
      }
    }
  `);

  const duration = 1500;
  const delay = 250;

  const [isLoaded, setIsLoaded] = useState(false);
  const [isEnded, setIsEnded] = useState(false);

  const onLoad = () => {
    setIsLoaded(true);
    setTimeout(() => {
      setIsEnded(true);
    }, duration + delay);
  };

  useEffect(() => {
    document.readyState === 'complete'
      ? onLoad()
      : window.addEventListener('load', onLoad);
  }, []);

  return (
    <ThemeProvider theme={theme}>
      <Helmet>
        <title>{title}</title>
        <meta name='description' content={description} />
        <meta property='og:title' content={title} />
        <meta property='og:site_name' content={title} />
        <meta property='og:type' content='website' />
        <meta property='og:url' content={`${siteUrl}/`} />
        <meta property='og:description' content={description} />
        <meta property='og:image' content={`${siteUrl}/ogp.jpg`} />
        <meta property='og:image:secure_url' content={`${siteUrl}/ogp.jpg`} />
        <meta property='og:image:type' content='image/png' />
        <meta property='og:image:width' content='1200' />
        <meta property='og:image:height' content='630' />
        <meta property='og:locale' content='ja_JP' />
        <style>
          {`
        html {
          background-color: #2e2e2e;
        }
        `}
        </style>
      </Helmet>
      {!isEnded && (
        <Box
          position='fixed'
          sx={{
            backgroundColor: '#000',
            transition: `opacity ${duration}ms ease-out ${delay}ms`,
            opacity: isLoaded ? 0 : 1,
            width: '100vw',
            height: '100vh',
            zIndex: 2000,
            pointerEvents: 'none'
          }}
        />
      )}
      <SectionMenu tel={tel} />
      <SectionMainvisual />
      <SectionAbout />
      <SectionAccess />
      <SectionContact tel={tel} />
    </ThemeProvider>
  );
};

export default IndexPage;
